import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { refs } from '../refs/refs';
import { markupSearchingIngredients } from '../templates/markupSearchingIngredients';
import { FAV_INGREDIENTS } from '../utils/constans';
import {
  clearDomELements,
  onReject,
  renderElementsMarkup,
} from '../utils/utils';

export async function getFavorIngredinents() {
  console.log(
    'ingreds in localStorage : ',
    await getDataLocalStorage(FAV_INGREDIENTS)
  );
  try {
    const result = await getDataLocalStorage(FAV_INGREDIENTS);

    onResolve(result);
  } catch (error) {
    onReject(error);
  }
}

export async function getSearchFavIngredientsByName(e) {
  const nameProduct = e.target.value.toLowerCase().trim();

  const favCoctailsArr = await getDataLocalStorage(FAV_INGREDIENTS);

  const newFilterArr = favCoctailsArr.filter(el =>
    el.strIngredient.toLowerCase().includes(nameProduct)
  );

  onResolve(newFilterArr);
}

function onResolve(arrFavIngreds) {
  const { favoriteIngredientsList, favoriteIngredientsSubTitle } = refs;

  if (arrFavIngreds.length === 0) {
    favoriteIngredientsSubTitle.textContent =
      "You haven't added any favorite ingridients yet";
    clearDomELements(favoriteIngredientsList);
    return;
  }

  renderElementsMarkup(
    favoriteIngredientsList,
    markupSearchingIngredients,
    arrFavIngreds
  );
}
